/**
 * Set the font size for the chatbox
 */
function resizeChatbox() {
    var chatContainerBox = document.getElementById("client_chatcontainer");
    var gameHeight = document.getElementById("client_background").offsetHeight;
    chatContainerBox.style.fontSize = "".concat((gameHeight * 0.0521).toFixed(1), "px");
    var trackstatus = (document.getElementById("client_trackstatustext"));
    trackstatus.width = (trackstatus.offsetWidth - 1) + "px";
    //clock
    var now = new Date();
    var weekday = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
    var month = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    document.getElementById("client_clock_month").innerText = month[now.getMonth()];
    console.debug(CHATBOX);
    if (CHATBOX == "acww") {
        weekday = ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"];
        document.getElementById("client_clock_weekday").innerText = weekday[now.getDay()];
        document.getElementById("client_clock_date").innerText = now.getDay() + "/" + now.getMonth();
        document.getElementById("client_clock_time").innerText = now.getHours() + ":" + now.getMinutes();
    }
    else if (CHATBOX == "key") {
        weekday = ["Sun.", "Mon.", "Tue.", "Wed.", "Thu.", "Fri.", "Sat."];
        document.getElementById("client_clock_weekday").innerText = weekday[now.getDay()];
        document.getElementById("client_clock_date").innerText = String(now.getDay());
    }
}
exports.resizeChatbox = resizeChatbox;
window.resizeChatbox = resizeChatbox;
/**
 * Update evidence icon.
 */
function updateEvidenceIcon() {
    var evidence_select = (document.getElementById("evi_select"));
    var evidence_filename = (document.getElementById("evi_filename"));
    var evidence_iconbox = (document.getElementById("evi_preview"));
    if (evidence_select.selectedIndex === 0) {
        evidence_filename.style.display = "initial";
        evidence_iconbox.src = "".concat(exports.AO_HOST, "evidence/").concat(encodeURI(evidence_filename.value.toLowerCase()));
    }
    else {
        evidence_filename.style.display = "none";
        evidence_iconbox.src = "".concat(exports.AO_HOST, "evidence/").concat(encodeURI(evidence_select.value.toLowerCase()));
    }
}
exports.updateEvidenceIcon = updateEvidenceIcon;
window.updateEvidenceIcon = updateEvidenceIcon;
