/**
 * Highlights and selects a menu.
 * @param {number} menu the menu to be selected
 */
function toggleMenu(menu) {
    if (menu !== selectedMenu) {
        document.getElementById("menu_".concat(menu)).className = "menu_button active";
        document.getElementById("content_".concat(menu)).className =
            "menu_content active";
        document.getElementById("menu_".concat(selectedMenu)).className = "menu_button";
        document.getElementById("content_".concat(selectedMenu)).className =
            "menu_content";
        selectedMenu = menu;
    }
}
exports.toggleMenu = toggleMenu;
window.toggleMenu = toggleMenu;
/**
 * Highlights and selects a shout for in-character chat.
 * If the same shout button is selected, then the shout is canceled.
 * @param {number} shout the new shout to be selected
 */
function toggleShout(shout) {
    if (shout === selectedShout) {
        document.getElementById("button_".concat(shout)).className = "client_button";
        selectedShout = 0;
    }
    else {
        document.getElementById("button_".concat(shout)).className = "client_button dark";
        if (selectedShout) {
            document.getElementById("button_".concat(selectedShout)).className =
                "client_button";
        }
        selectedShout = shout;
    }
}
exports.toggleShout = toggleShout;
window.toggleShout = toggleShout;
